import React from 'react';

const Card = (props) =>{
    return(
        <div className='bg-light-blue dib b-black br2 pa2 ma2 grow shadow-4'>
            <img alt='robo pics' src={`https://robohash.org/${props.id}mayur`}/>
            <div>
                <h2>{props.id}</h2>
                <p>{props.name}</p>
                <p>{props.email }</p>
            </div>
        </div>
    );
} 

export default Card;